/* Global reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-smoothing: antialiased;
}

body {
  font-family: 'Roboto', sans-serif;
  background-color: #0f0f0f; /* Deep matte black */
  color: #e0e0e0; /* Light text */
  line-height: 1.6;
  margin: 0;
  overflow-x: hidden;
}

/* Hero Section */
.hero-section {
  height: 100vh;
  background: url('https://your-image-url.com/wilderness.jpg') no-repeat center center/cover;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 1;
  overflow: hidden;
}

.hero-section:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7); /* Dark overlay for better text readability */
  z-index: -1;
}

.hero-content {
  text-align: center;
  max-width: 700px;
  padding: 20px;
  animation: fadeInUp 1s ease-out;
}

/* Hero Logo */
.hero-logo {
  width: 400px; /* Increased logo size */
  height: auto;
  margin-bottom: -90px;
  opacity: 0;
  animation: fadeInDown 1.2s ease-out forwards;
}

/* Hero Title */
.hero-title {
  font-family: 'Poppins', sans-serif;
  font-size: 3.5rem; /* Adjusted size to fit on one line */
  color: #ececec;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-bottom: 20px;
  white-space: nowrap; /* Prevents line breaks */
}

.hero-subtitle {
  font-family: 'Poppins', sans-serif;
  font-size: 1.8rem;
  color: #d4af37; /* Muted gold for elegance */
  margin-bottom: 30px;
}

.hero-description {
  font-size: 1.2rem;
  color: #c0c0c0;
  margin-bottom: 40px;
}

.hero-buttons {
  display: flex;
  justify-content: center;
  gap: 20px;
}

/* Buttons */
button {
  background-color: #d4af37; /* Muted gold */
  color: #0d0d0d;
  padding: 12px 24px;
  font-size: 1rem;
  border-radius: 50px;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: bold;
  text-transform: uppercase;
}

button:hover {
  background-color: #bfa13b; /* Slightly darker on hover */
  transform: translateY(-2px); /* Lift effect */
}

button:active {
  transform: translateY(0);
}

/* Input Fields */
input[type="text"], input[type="password"], textarea {
  width: 100%;
  padding: 15px;
  margin-bottom: 20px;
  border-radius: 10px;
  background-color: #1c1c1c;
  color: #fff;
  border: 2px solid #444;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
  font-size: 1rem;
}

input:focus, textarea:focus {
  border-color: #d4af37; /* Gold outline when focused */
  box-shadow: 0 0 10px rgba(212, 175, 55, 0.7); /* Glowing effect */
  outline: none;
}

/* Remove the Scroll Icon */
.scroll-icon {
  display: none;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translateY(-30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Responsive Design */
@media (max-width: 768px) {
  .hero-title {
    font-size: 2.6rem; /* Adjusted for mobile */
  }

  .hero-subtitle {
    font-size: 1.5rem;
  }

  .hero-description {
    font-size: 1rem;
  }

  .hero-logo {
    width: 200px; /* Smaller logo for mobile */
  }

  .hero-buttons {
    flex-direction: column;
  }

  button {
    width: 100%;
    padding: 14px;
  }
}
